/*     */
.login-page-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
}

/*     */
.login-form-container {
  width: 100%; /*    100%  */
  max-width: 400px; /*    */
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 40px;
  margin: 0 auto; /*     */
}



/*          */
.login-form h2 {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 600;
}

/*     */
.login-form label {
  display: block;
  margin-bottom: 5px; /*       */
  font-size: 16px;
}

/*      */
.login-form-input {
  width: calc(100% - 20px); 
  padding: 10px;
  margin-bottom: 15px; /*      */
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

/*       */
.login-form-button {
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  background-color: #007bff; /*    */
  color: white;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.login-form-button:hover {
  background-color: #0056b3; /*      */
}

/*   */
@media (max-width: 600px) {
  .login-form-container {
    padding: 20px;
  }

  .login-form h2 {
    font-size: 20px;
  }

  .login-form-input,
  .login-form-button {
    font-size: 14px;
  }
}

.register-form-button {
  width: 100%;
  padding: 10px;
  margin-top: 10px; /*    */
  background-color: white; /*   */
  color: black; /*   */
  font-size: 18px;
  border: 1px solid #ddd; /*   */
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.register-form-button:hover {
  background-color: #f8f8f8; /*     */
}

/*    */
.login-form-button.register-form-button {
  background-color: #fff; /*   */
  color: #000; /*   */
  border: 1px solid #ddd; /*  */
  margin-top: 15px; /*    */
}

/*     */
.login-form-button.register-form-button:hover {
  background-color: #f8f8f8; /*      */
}

.login_text {
  text-align: center;
  font-size:x-large;
  padding-bottom: 25px;
  font-weight: bold;
}