PatientRecord.css

/*        */

.date-search-form input[type="date"] {
  font-size: 16px;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #f0f0f0;
  border-radius: 4px;
}
.clicked-date {
  background-color: gray !important;
  color: white !important;
}

.highlight {
  background-color: rgb(27, 107, 237) !important;
  color: white !important;
}

.date-search-form button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 20px; /*     */
}

/*   */
.date-search-form + .medical-records-results {
  border-top: 2px solid #ccc;
  padding-top: 20px;
  margin-top: 60px;
}

/*     */
.medical-records-results {
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/*        */
.medical-records-section {
  margin-top: 20px;
}

/*          */
.medical-records-results, .medical-records-section p {
  max-width: 70%; /*     */
  margin: 20px auto; /*     0,   auto    */
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-top: 20px; /*     */
}

.medical-records-section h2 {
  text-align: center; /*   */
  font-size: 1.5em; /*    */
  margin-top: 20px; /*    */
  margin-bottom: 20px; /*    */
}

/*       */
.medical-records-section button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: block; /*    */
  margin: 10px auto; /*    */
  font-size: 1em; /*   */
}

@media (max-width: 768px) {
  .date-search-form {
    flex-direction: column;
    width: 100%;
  }
  .medical-records-results, .medical-records-section p {
    max-width: 100%; /*     100%  */
  }
  .medical-records-section h2 {
    font-size: 1.2em; /*      */
  }
}